<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <b></b>
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
      <el-table :data="tableData" :span-method="objectSpanMethod" border style="width: 100%">
        <el-table-column prop="date" label="" sortable width="180" align="center" :filters="[{text: '2016-05-01', value: '2016-05-01'}, {text: '2016-05-02', value: '2016-05-02'}, {text: '2016-05-03', value: '2016-05-03'}, {text: '2016-05-04', value: '2016-05-04'}]" :filter-method="filterHandler">
        </el-table-column>
        <el-table-column prop="name" label="" width="300" align="center">
        </el-table-column>
        <el-table-column prop="address" label="" width="100" align="center" :formatter="formatter">
          <template slot-scope="scope">
            <el-tag :type="scope.row.address === '' ? 'primary' : 'success'" disable-transitions>{{scope.row.address}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="tag" label="" width="100" align="center" :filter-method="filterTag">
          <template slot-scope="scope">
            <el-tag :type="scope.row.tag === '' ? 'primary' : 'success'" disable-transitions>{{scope.row.tag}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
  </el-row>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "//",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "SRP",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "SRP",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "SRP//",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "SRP//",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "",
          address: "",
          tag: ""
        },
        {
          date: "SRP",
          name: "",
          address: "",
          tag: ""
        }
      ]
    };
  },
  methods: {
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex % 2 === 0) {
          return {
            rowspan: 2,
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    }
  }
};
</script>



