<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <b></b>
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" style="float:right;">
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table :data="datapage" highlight-current-row @selection-change="selsChange" stripe style="width: 100%;">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="userId" label="id" width="70" align="center"></el-table-column>
        <el-table-column prop="userName" label="" align="center"></el-table-column>
        <el-table-column prop="role" label="" width="100" :formatter="formatRole" align="center"></el-table-column>
        <el-table-column prop="srpnames" label="SRP" :formatter="formatSrpname" align="center"></el-table-column>
        <el-table-column prop="phone" label="" align="center"></el-table-column>
        <el-table-column prop="email" label="" align="center"></el-table-column>
        <el-table-column prop="remark" label="" align="center"></el-table-column>
        <el-table-column label="" align="center" width="200">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button size="small" icon="el-icon-edit" style=" font-size: 16px;" @click="showEditDialog(scope.$index,scope.row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" v-if="scope.row.role===2 " effect="dark" content="" placement="top">
                <el-button size="small" icon="el-icon-setting" style=" font-size: 16px;" @click="showPWDDialog(scope.$index,scope.row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" v-if="scope.row.role===2 " effect="dark" content="" placement="top">
                <el-button size="small" icon="el-icon-delete" style=" font-size: 16px;" @click="delUser(scope.$index,scope.row)"></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="total,prev, pager, next,jumper" background @current-change="handleCurrentChange" :page-size="pagesize" :total="data.length" style="float:right;">
        </el-pagination>
      </el-col>

      <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="ID" prop="userId">
            <el-input v-model="editForm.userId" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="editForm.userName" auto-complete="off"></el-input>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-select v-model="editForm.role" placeholder="">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled">
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="phone">
            <el-input v-model.number="editForm.phone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="remark">
            <el-input type="textarea" v-model="editForm.remark" :rows="8"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"></el-button>
          <el-button type="primary" @click.native="editSubmit"></el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" status-icon label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="" prop="userName">
            <el-input v-model="addForm.userName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="addForm.role" placeholder="">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model.number="addForm.phone" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="userPwd">
            <el-input type="password" v-model="addForm.userPwd" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input type="password" v-model="addForm.checkPass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="remark">
            <el-input type="textarea" v-model="addForm.remark" :rows="8"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false"></el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        </div>
      </el-dialog>

      <el-dialog title="" :visible.sync="pwdFormVisible" :close-on-click-modal="false">
        <el-form :model="pwdForm" label-width="100px" :rules="pwdFormRules" ref="pwdForm">
          <el-form-item label="ID" prop="userId">
            <el-input v-model="pwdForm.userId" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="userPwd">
            <el-input type="password" v-model="pwdForm.userPwd" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input type="password" v-model="pwdForm.checkPass" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="pwdFormVisible = false"></el-button>
          <el-button type="primary" @click.native="pwdSubmit"></el-button>
        </div>
      </el-dialog>

    </el-col>
  </el-row>
</template>
<script>
import API from "../../api/api_user";

export default {
  data() {
    var CheckTel = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else if (!Number.isInteger(value)) {
        callback(new Error(""));
      } else if (value.toString().length != 11) {
        callback(new Error("11"));
      } else {
        // API.CheckPhon(value).then(
        //   function(result) {
        //     that.loading = false;
        //     if (result.code===0) {
        //       callback();
        //     } else {
        //       callback(new Error(""));
        //     }
        //   },
        //   function(err) {
        //     that.loading = false;
        //     that.$message.error({
        //       showClose: true,
        //       message: err.toString(),
        //       duration: 3000
        //     });
        //   }
        // );
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        if (this.addForm.checkPass !== "") {
          this.$refs.addForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else if (value !== this.addForm.userPwd) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    var validatePass3 = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        if (this.pwdForm.checkPass !== "") {
          this.$refs.pwdForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass4 = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else if (value !== this.pwdForm.userPwd) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };

    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        callback(new Error(""));
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback();
        } else {
          callback(new Error(""));
        }
      }, 100);
    };

    return {
      options2: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        }
      ],
      role: "2",
      data: [],
      datapage: [],
      total: 0,
      page: 1,
      pagesize: 15,
      loading: false,
      sels: [], //

      //
      pwdFormVisible: false, //
      pwdForm: {
        userId: "",
        userPwd: "",
        checkPass: ""
      },
      pwdFormRules: {
        userPwd: [
          {
            validator: validatePass3,
            required: true,
            trigger: "blur"
          },
          {
            min: 6,
            max: 8,
            message: "68",
            trigger: "blur"
          }
        ],
        checkPass: [
          {
            validator: validatePass4,
            required: true,
            trigger: "blur"
          }
        ]
      },

      //
      editForm: {
        userId: "",
        userName: "",
        phone: "",
        email: "",
        remark: "",
        role: ""
      },
      editFormVisible: false, //
      editFormRules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: " 20 ", trigger: "blur" }
        ],
        phone: [
          {
            required: true,
            validator: CheckTel,
            trigger: "blur"
          }
        ],
        email: [
          {
            validator: checkEmail,
            required: true,
            trigger: "blur"
          }
        ],
        remark: [{ max: 100, message: "200", trigger: "blur" }]
      },
      addForm: {
        userName: "",
        phone: "",
        email: "",
        userPwd: "",
        checkPass: "",
        remark: "",
        role: ""
      },

      //
      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: " 20 ", trigger: "blur" }
        ],
        phone: [
          {
            required: true,
            validator: CheckTel,
            trigger: "blur"
          }
        ],
        email: [
          {
            validator: checkEmail,
            required: true,
            trigger: "blur"
          }
        ],
        userPwd: [
          {
            validator: validatePass,
            required: true,
            trigger: "blur"
          },
          {
            min: 6,
            max: 8,
            message: "68",
            trigger: "blur"
          }
        ],
        checkPass: [
          {
            validator: validatePass2,
            required: true,
            trigger: "blur"
          }
        ],
        remark: [{ max: 100, message: "200", trigger: "blur" }]
      }
    };
  },
  methods: {
    formatRole: function(row, column) {
      return row.role == "1"
        ? ""
        : row.role == "2" ? "" : "null";
    },
    formatSrpname: function(row, column) {
      return row.role == "1"
        ? ""
        : row.srpnames == "null" ? "" : row.srpnames;
    },
    handleCurrentChange(val) {
      this.datapage = [];
      this.datapage = this.data.slice(
        (val - 1) * this.pagesize,
        val * this.pagesize
      );
    },
    search() {
      let that = this;

      that.loading = true;
      API.findList()
        .then(
          function(result) {
            that.loading = false;
            if (result && result.data) {
              that.data = result.data;
              that.datapage = that.data.slice(0, that.pagesize);
            }
          },
          function(err) {
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: err.toString(),
              duration: 3000
            });
          }
        )
        .catch(function(error) {
          that.loading = false;
          console.log(error);
          that.$message.error({
            showClose: true,
            message: "",
            duration: 3000
          });
        });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    delUser: function(index, row) {
      let that = this;
      var tishi = row.srpnames;
      if (tishi === "null") {
        tishi = "";
      }
      this.$confirm(
        "SRP:" + tishi + "  ?",
        "",
        {
          type: "warning"
        }
      )
        .then(() => {
          that.loading = true;
          let param = {
            userId: row.userId
          };
          API.remove(param)
            .then(
              function(result) {
                that.loading = false;
                if (result && parseInt(result.code) === 0) {
                  that.$message.success({
                    showClose: true,
                    message: ",",
                    duration: 1500
                  });
                  that.search();
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 3000
                });
              }
            )
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 3000
              });
            });
        })
        .catch(() => {});
    },
    //
    showEditDialog: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
      this.$refs["editForm"].resetFields();
    },
    //
    editSubmit: function() {
      let that = this;
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.loading = true;
          API.changeProfile(this.editForm)
            .then(
              function(result) {
                that.loading = false;
                if (result && parseInt(result.code) === 0) {
                  that.$message.success({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                  that.$refs["editForm"].resetFields();
                  that.editFormVisible = false;
                  that.search();
                } else {
                  that.$message.error({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 3000
                });
              }
            )
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 3000
              });
            });
        }
      });
    },
    //
    showAddDialog: function() {
      this.addFormVisible = true;
      this.addForm = {
        userName: "",
        phone: "",
        email: "",
        userPwd: "123456",
        checkPass: "123456",
        remark: "",
        role: "2"
      };
      this.$refs["addForm"].resetFields();
    },
    //
    addSubmit: function() {
      let that = this;
      this.$refs.addForm.validate(valid => {
        if (valid) {
          that.loading = true;
          console.log(this.addForm);
          API.insertProfile(this.addForm)
            .then(
              function(result) {
                that.loading = false;
                if (result && parseInt(result.code) === 0) {
                  that.$message.success({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                  that.$refs["addForm"].resetFields();
                  that.addFormVisible = false;
                  that.search();
                } else {
                  that.$message.error({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 3000
                });
              }
            )
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 3000
              });
            });
        }
      });
    },
    //
    batchDeleteBook: function() {
      let ids = this.sels.map(item => item.userId).toString();
      let that = this;
      this.$confirm(
        "SRP",
        "",
        {
          type: "warning"
        }
      )
        .then(() => {
          that.loading = true;
          API.removeList(ids)
            .then(
              function(result) {
                that.loading = false;
                if (result && parseInt(result.code) === 0) {
                  that.$message.success({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                  that.search();
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 3000
                });
              }
            )
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 3000
              });
            });
        })
        .catch(() => {});
    },
    //
    showPWDDialog: function(index, row) {
      this.pwdFormVisible = true;
      this.pwdForm = {
        userId: row.userId,
        userPwd: "",
        checkPass: ""
      };
      this.$refs["pwdForm"].resetFields();
    },
    pwdSubmit: function() {
      let that = this;
      this.$refs.pwdForm.validate(valid => {
        if (valid) {
          this.loading = true;
          API.changePwd(this.pwdForm)
            .then(
              function(result) {
                that.loading = false;
                if (result && parseInt(result.code) === 0) {
                  that.$message.success({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                  that.pwdFormVisible = false;
                  that.search();
                } else {
                  that.$message.error({
                    showClose: true,
                    message: "",
                    duration: 3000
                  });
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 3000
                });
              }
            )
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "",
                duration: 3000
              });
            });
        }
      });
    }
  },
  mounted() {
    this.search();
  }
};
</script>

<style>
.demo-table-expand label {
  font-weight: bold;
}
</style>


